<template>
  <div class="home">
    <div class="container">
      <div class="search-engine">
        <div
          class="search-logo"
          :class="baidu ? 'search-logo-transform0' : 'search-logo-transform360'"
        >
          <img
            style="width:100%;height:100%"
            alt="搜索引擎Logo"
            title="点击切换"
            :src="baidu ? '/baidu.png' : '/google.png'"
            class=""
            @click="baidu = !baidu"
          />
        </div>
        <div class="search-box">
          <form action="" class="search-from">
            <span
              class="search-input-box"
              :class="
                (searchFocus ? 'search-input-box-foucs' : '',
                searchFocus && arr.length > 0 ? 'search-input-halfradius' : '')
              "
            >
              <!-- <input type="text" class="search-input" /> -->
              <input
                id="search-input"
                tabindex="1"
                type="text"
                class="search-input"
                autocomplete="off"
                @focus="inputFocus()"
                @blur="inputBlur()"
                @keyup="keyup($event)"
                @keydown="keydown($event)"
                v-model="wd"
              />
            </span>
            <span
              class="search-button-box"
              :class="
                searchFocus && arr.length > 0 ? 'search-input-halfradius2' : ''
              "
            >
              <input @click="search" type="button" class="search-button" />
            </span>
          </form>
          <div class="search-suggest" v-show="searchFocus && arr.length > 0">
            <div
              class="suggest-item"
              :class="index === listIndex ? 'suggest-item-current' : ''"
              v-for="(item, index) in arr"
              :key="index"
              @mousedown="suggestClick(item)"
            >
              <span class="suggest-keyword">{{ item }}</span>
            </div>
          </div>
        </div>
        <div class="web-site" :class="searchFocus ? 'hidden' : ''">
          <!-- <div class="web-site-header">
            <div class="web-site-header-body"></div>
          </div> -->
          <div class="web-site-body">
            <div
              class="web-group web-group-couple site-list-item-transform0"
              v-for="(data, di) in json"
              :key="di"
            >
              <div class="web-group-head">
                <span class="title-font">{{ data.className }}</span>
              </div>
              <a
                :href="site.siteUrl"
                class="web-group-item"
                v-for="(site, si) in data.siteVos"
                :key="si"
              >
                <div
                  class="web-group-icon"
                  :style="'background-image: url(' + site.logoResourceId + ');'"
                ></div>
                <span class="web-group-name">{{ site.siteName }}</span>
              </a>
            </div>
          </div>
        </div>
        <div v-if="searchFocus" id="home-mask"></div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
//import HelloWorld from "@/components/HelloWorld.vue";
import json from "@/data/data_1.json";
export default {
  name: "Home",
  // components: {
  //   HelloWorld
  // }
  created() {
    //console.log(json.dataResult);
  },
  data() {
    return {
      baidu: true,
      searchFocus: false,
      json: json.dataResult,
      wd: "",
      arr: [], //用于储存关键词的搜索词条
      listIndex: -1 //设置初始索引，数组从0开始，因此初始成-1
    };
  },
  methods: {
    keyup(event) {
      //如果我按的是上下键，那么就不发送请求了
      // console.log(event);
      if (event.keyCode == 38 || event.keyCode == 40 || event.keyCode == 13)
        return;
      const url = "https://sp0.baidu.com/5a1Fazu8AA54nxGko9WTAnF6hhy/su";
      this.$http
        .jsonp(url, {
          params: {
            wd: this.wd
          },
          jsonp: "cb"
        })
        .then(res => {
          // console.log(res);
          this.arr = res.data.s; //把百度服务器返回的数据传给arr数组
        });
    },
    //监听鼠标的点击事件
    //如果鼠标点击某一行的文字，则点击事件中的event.explicitOriginalTarget.data代表关键词
    //如果点击某一行的空白处，则点击事件中的event.explicitOriginalTarget.innerText代表关键词
    //大家可以通过console.log(event)来查看关键词所在的位置
    suggestClick(data) {
      this.wd = data;
      if (this.baidu) {
        window.open(`https://www.baidu.com/s?wd=` + this.wd);
      } else {
        window.open(`https://www.google.com/search?q=` + this.wd);
      }
      // if (event.explicitOriginalTarget.data != undefined) {

      // } else if (event.explicitOriginalTarget.innerText != undefined) {
      //   this.wd = event.explicitOriginalTarget.innerText;
      //   window.open("https://www.baidu.com/s?wd=" + this.wd);
      // }
    },
    //监听键盘的事件
    //如果按down，则增加当前listIndex+1，因此arr[this.listIndex]就能代表当前的词条
    //我们通过对listIndex的修改来得到当前词条在arr中的索引，然后就可以得到词条的具体信息，然后发送请求了
    keydown(event) {
      //console.log(event);
      //下键：40 上键：38
      if (event.keyCode == 38) {
        //按的上键
        this.listIndex--;
        if (this.listIndex < 0) {
          this.listIndex = this.arr.length - 1;
        }
        this.wd = this.arr[this.listIndex];
      } else if (event.keyCode == 40) {
        //说明你按的是下键
        this.listIndex++;
        if (this.listIndex > this.arr.length - 1) {
          this.listIndex = 0;
        }
        this.wd = this.arr[this.listIndex];
      } else if (event.keyCode == 13) {
        //如果你按的是enter，那么就跳转到百度搜索结果
        // window.open("https://www.baidu.com/s?wd=" + this.wd);
        if (this.baidu) {
          window.open(`https://www.baidu.com/s?wd=` + this.wd);
        } else {
          window.open(`https://www.google.com/search?q=` + this.wd);
        }
      }
    },
    search() {
      if (this.baidu) {
        window.open(`https://www.baidu.com/s?wd=` + this.wd);
      } else {
        window.open(`https://www.google.com/search?q=` + this.wd);
      }
    },
    inputFocus() {
      this.searchFocus = true;
    },
    inputBlur() {
      this.searchFocus = false;
      // setTimeout(() => {
      //   this.searchFocus = false;
      // }, 400);
    }
  }
};
</script>

